import {
  Body,
  Controller,
  HttpCode,
  NotImplementedException,
  Patch,
  UseGuards,
} from '@nestjs/common';
import { AuthGuard } from '@nestjs/passport';
import {
  ApiBadRequestResponse,
  ApiBearerAuth,
  ApiInternalServerErrorResponse,
  ApiNotFoundResponse,
  ApiOkResponse,
  ApiOperation,
  ApiTags,
  ApiUnauthorizedResponse,
} from '@nestjs/swagger';

import { type IDecodedToken } from '../../../application/interface';
import { CustomLogger } from '../../../utility';
import { CurrentDecodedToken } from '../../decorator';
import { UpdatePersonnelRequest } from '../request';
import { PersonnelResponse } from '../response';

@ApiTags('personnels')
@ApiBearerAuth()
@UseGuards(AuthGuard('bearer'))
@Controller('personnels')
export class PersonnelUpdateController {
  constructor(readonly logger: CustomLogger) {
    this.logger.setContext(PersonnelUpdateController.name);
  }

  @Patch()
  @ApiOperation({
    description: 'Update Personnel',
  })
  @ApiOkResponse({
    type: PersonnelResponse,
  })
  @ApiNotFoundResponse({
    description: 'Personnel not found',
  })
  @ApiBadRequestResponse({
    description: 'リクエスト内容が不正',
  })
  @ApiInternalServerErrorResponse({
    description: 'サーバーエラー',
  })
  @ApiUnauthorizedResponse({
    description: '認証エラー',
  })
  @HttpCode(200)
  async update(
    @CurrentDecodedToken() decodedToken: IDecodedToken,
    @Body() body: UpdatePersonnelRequest,
  ): Promise<PersonnelResponse> {
    throw new NotImplementedException({
      message: 'Method not implemented',
      decodedToken,
      body,
    });
  }
}
